@import "../../fonts/circular-std/stylesheet.css";
@import "../../fonts/newyork/stylesheet.css";
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "New York Extra Large" !important;
  font-weight: 600;
}

.text-orange {
  color: #EE9D3E !important;
}

.iris-button {
  min-width: 150px;
  justify-content: center;
  background: linear-gradient(95.56deg, #5A577B 0%, #2A2840 104.29%) !important;
}

.button-shadow {
  -webkit-box-shadow: 0 10px 50px -12px #2A2840 !important;
  -moz-box-shadow: 0 10px 50px -12px #2A2840 !important;
  box-shadow: 0 10px 50px -12px #2A2840 !important;
  transition: all 0.15s ease-in-out !important;
}

.cover-section--wrapper::before {
  content: "";
  position: absolute;
  height: 60vh;
  width: 50vw;
  background: url("../../img/cover-blured-bg.svg");
  background-size: contain;
  background-position: center left;
  background-repeat: no-repeat;
  top: 0;
  left: 0;
}

.product-showcase::before {
  background: url("../../img/green-leaf-2.png");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  height: 15%;
  left: -0.5%;
  position: absolute;
  top: 2%;
  width: 10vw;
}
@media (max-width: 575px) {
  .product-showcase::before {
    display: none;
  }
}
.product-showcase::after {
  position: absolute;
  left: 0;
  bottom: 0;
  background: #E3F3FF;
  height: 1700px;
  width: 100%;
  content: "";
  z-index: -1;
}
@media (max-width: 575px) {
  .product-showcase::after {
    height: 400px;
  }
}
.product-showcase .zb-section__innerWrapper::after {
  background: url("../../img/green-leaf-home-2.png");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  bottom: -1px;
  content: "";
  position: absolute;
  right: -2vw;
  width: 550px;
  z-index: 99;
  min-height: 350px;
}
@media (max-width: 575px) {
  .product-showcase .zb-section__innerWrapper::after {
    background: url("../../img/green-leaf.png");
    right: 0;
    bottom: -140px;
    background-repeat: no-repeat;
    background-position: -220% 50%;
    background-size: 110%;
    width: 150px;
    height: 150px;
    min-height: fit-content;
  }
}

.customer-review-section-img::after {
  background: url("../../img/green-leaf.png");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  bottom: -80px;
  content: "";
  height: 200px;
  left: -120px;
  position: absolute;
  width: 280px;
}
@media (max-width: 575px) {
  .customer-review-section-img::after {
    bottom: -40px;
    height: 120px;
    width: 120px;
    left: -25px;
  }
}

.home-best-seller-area::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 470px;
  left: 0;
  bottom: 0;
  background-color: #F7F1EA;
}
@media (max-width: 575px) {
  .home-best-seller-area::before {
    bottom: -60px;
    height: 100%;
  }
}

.iris-accordion .zb-el-accordions-accordionTitle {
  font-family: "Circular Std Book";
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;
  color: #000;
}
@media (max-width: 575px) {
  .iris-accordion .zb-el-accordions-accordionTitle {
    font-size: 16px;
    line-height: 20px;
  }
}

.zb-el-accordionItem.zb-el-accordions--active .zb-el-accordions-accordionIcon::before {
  font-size: 25px;
  color: transparent;
  background: url("../../img/accordion-active.svg");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

.zb-el-accordionItem .zb-el-accordions-accordionIcon::before {
  font-size: 25px;
  color: transparent;
  background: url("../../img/accordion-idle.svg");
  background-size: 100%;
  background-position: center center;
  background-repeat: no-repeat;
}

.contant-us-content {
  box-shadow: 0px 30px 142px -99px rgba(0, 0, 0, 0.55);
}
@media (max-width: 575px) {
  .contant-us-content {
    box-shadow: none;
  }
}

.wpforms-container {
  margin: 0;
}
.wpforms-container form {
  background: #F7F1EA;
  padding: 20px 30px;
  border-radius: 12px;
}
.wpforms-container form .wpforms-submit {
  background: #C17923 !important;
  border-radius: 10px !important;
  color: #fff !important;
  font-family: "Circular Std book";
  font-weight: 100 !important;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.02em;
  transition: all 0.35s ease-in-out;
  border: none;
}
.wpforms-container form .wpforms-submit:hover {
  background-color: #b06b19 !important;
}
.wpforms-container form .wpforms-field-label {
  font-weight: 300 !important;
  font-size: 16px !important;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #625C56;
}
.wpforms-container form .wpforms-field input {
  background: #FFFFFF;
  transition: all 0.25s ease-in-out;
  border-radius: 10px !important;
  border: 2px solid transparent !important;
}
.wpforms-container form .wpforms-field input:focus {
  border: 2px solid #E6D1BA !important;
}
.wpforms-container form .wpforms-field input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.02em;
  color: #BFBFBF;
}
.wpforms-container form .wpforms-field textarea {
  background: #FFFFFF;
  border-radius: 10px !important;
  transition: all 0.25s ease-in-out;
  border: 2px solid transparent !important;
}
.wpforms-container form .wpforms-field textarea:focus {
  border: 2px solid #E6D1BA !important;
}
.wpforms-container form .wpforms-field textarea::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.02em;
  color: #BFBFBF;
}
.wpforms-container form .wpforms-submit-container {
  text-align: right;
}

.home_mini_cart {
  display: flex;
  margin-top: 10px !important;
}
.home_mini_cart .home_quantity {
  width: 110px;
  margin-right: 15px;
}
.home_mini_cart .home_quantity input[type=number]::-webkit-inner-spin-button {
  opacity: 1;
}
.home_mini_cart .single_add_to_cart_button {
  background: #1a202c;
}

.woocommerce-page .button {
  background: linear-gradient(95.56deg, #5A577B 0%, #2A2840 104.29%) !important;
  font-family: "New York Extra Large";
  font-weight: 400;
}
.woocommerce-page .woo-archive-btn-button .button {
  width: 170px !important;
  margin-left: auto;
  margin-right: auto;
  -webkit-box-shadow: 0 10px 50px -12px #2A2840 !important;
  -moz-box-shadow: 0 10px 50px -12px #2A2840 !important;
  box-shadow: 0 10px 50px -12px #2A2840 !important;
  transition: all 0.15s ease-in-out !important;
}
.woocommerce-page .woo-archive-btn-button .button:hover {
  transform: translateY(-5px);
}
.woocommerce-page .product-archive .product-details .product-action-wrap {
  margin-bottom: 3em;
}
.woocommerce-page .product-archive .product-details .woocommerce-loop-product__title {
  text-align: center;
}
.woocommerce-page .product-archive .product-details .price {
  text-align: center;
  font-family: "Circular Std";
  font-size: 18px !important;
  letter-spacing: 1px;
}